import React from 'react';
import { Box, Typography, Button, Card, CardMedia, CardContent, CardActions, Chip } from "@material-ui/core";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import PhoneIcon from "@material-ui/icons/Phone";
import { Rating } from '@material-ui/lab';

import useStyles from "./styles";

const PlaceDetail = ({ name, photo, price_level, ranking, awards, cuisine, address, phone, web_url, rating, website, num_reviews, selected, refProp }) => {
  const classes = useStyles();

  if(selected) refProp?.current?.scrollIntoView({behavior: "smooth", block: "start"});
  
  return (
    <Card elevation={6}>
      <CardMedia
        style={{ height: 350 }}
        image={photo ? photo.images.large.url : "Loading..."}
        title={name}
      />
      <CardContent>
        <Typography gutterBottom variant="h5">{name}</Typography>
        <Box display="flex" justifyContent="space-between">
        <Rating size="small" value={Number(rating)} readOnly />
          <Typography gutterBottom variant="subtitle1">out of {num_reviews} reviews</Typography>
        </Box>

        <Box display="flex" justifyContent="space-between">
          <Typography variant="subtitle1">Price</Typography>
          <Typography gutterBottom variant="subtitle1">{price_level}</Typography>
        </Box>
        <Box display="flex" justifyContent="space-between">
          <Typography variant="subtitle1">Ranking</Typography>
          <Typography gutterBottom variant="subtitle1">{ranking}</Typography>
        </Box>
        {awards?.map((award) => (
          <Box my={1} display="flex" justifyContent="space-between" alignItems="center">
            <img src={award.images.small} alt={award.display_name} />
            <Typography variant='subtitle2' color="textSecondary">{award.display_name}</Typography>
          </Box>
        ))}
        {cuisine?.map(({name}) => (
          <Chip key={name} size="small" label={name} className={classes.chip} />  
        ))}

        {address && (
          <Typography gutterBottom variant='subtitle2' color="textSecondary" className={classes.subtitle}>
            <LocationOnIcon />{address}
          </Typography>
        )}

        {phone && (
          <Typography gutterBottom variant='subtitle2' color="textSecondary" className={classes.spacing}>
            <PhoneIcon />{phone}
          </Typography>
        )}
        <CardActions>
          <Button size="small" color="primary" onClick={() => window.open(web_url,"_blank")}>
            Trip Advisor
          </Button>
          <Button size="small" color="primary" onClick={() => window.open(website,"_blank")}>
            Website
          </Button> 
        </CardActions>
      </CardContent>
    </Card>
  )
}

export default PlaceDetail